<!--  -->
<template>
  <div class="main">
    <el-container class="main_con">
      <el-aside width="20%" class="con_l">
        <div class="con">
          <div class="tabs flex_c">
            <template v-for="(item,index) in rankTags">
              <span :key="index" :class="{active:tagsNum==index}" @click="tagsNum=index;tagsListNum=0">{{item.name}}</span>
            </template>
          </div>
          <ul class="tab_con">
            <template v-for="(item,index) in tagList">
              <li :key="index" class="flex_c" :class="{active:tagsListNum==index}" @click="tagsListNum = index">
                <div class="item_info">
                  <p class="name">{{item.name}}</p>
                  <p class="time">{{item.pub}}</p>
                </div>
              </li>
            </template>
          </ul>
        </div>
      </el-aside>
      <el-main class="con_r">
        <!-- 列表 -->
        <div>
          <div>
            <span class="title">{{title}}</span>
            <span class="update_time">更新时间：{{updateTime}}</span>
          </div>
          <div class="btns">
            <el-button class="play" round>
              <i class="iconfont icon-icon_play_"></i>
              <span>播放全部</span>
            </el-button>
          </div>
          <!-- 列表组件 -->
          <div class="list_out">
            <music-table :pageNum="pageNum" :songList="rankList" v-show="code==200"></music-table>
            <music-no-data v-show="code!=200 & code!=0"></music-no-data>
          </div>
          <pagination :code="code" :currentChange="getRankList" :pageSize="30" :total="total"></pagination>

        </div>
        <!-- 评论区 -->
        <div>
        </div>

      </el-main>
    </el-container>
  </div>
</template>

<script>
import { HttpManager } from '../api';
import MusicNoData from '../components/MusicNoData.vue';
import MusicTable from '../components/MusicTable.vue';
import Pagination from '../components/Pagination.vue';

export default {
  name: "RankList",
  data () {
    return {
      code: 0,
      rankTags: [],
      tagList: [],
      rankList: [],
      tagsNum: 0,
      tagsListNum: 0,
      total: 0,
      rankId: 93,
      title: "",
      updateTime: "",
      pageNum: 0,
    };
  },

  components: { MusicTable, MusicNoData, Pagination },

  methods: {
    getRankList (val) {
      let that = this;
      this.pageNum = val;
      //自动回顶部
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      HttpManager.getBangList({ bangId: this.rankId, pn: val, rn: 30 })
        .then((res) => {
          that.updateTime = res.data.pub;
          that.code = parseInt(res.code);
          that.total = parseInt(res.data.num);
          that.rankList = res.data.musicList;
        })
        .catch((err) => { console.log(err) });
    },
    getTagRank () {
      let that = this;
      HttpManager.getTagBang()
        .then((res) => {
          let arr = [];
          arr.push(res.data[0], res.data[2]);
          that.rankTags = arr;
          that.tagList = that.rankTags[0].list;
          that.title = that.tagList[0].name;
        }).catch((err) => {
          console.log(err);
        });
    }
  },

  computed: {},
  watch: {
    tagsNum: {
      handler (newVal, oldVal) {
        console.log(newVal)
        this.tagList = this.rankTags[this.tagsNum].list;
        this.rankId = this.tagList[this.tagsListNum].sourceid;
        this.title = this.tagList[this.tagsListNum].name;
        this.getRankList(1);

      },
      // immediate: true, //监听首次绑定值
    },
    tagsListNum: {
      handler (newVal, oldVal) {
        this.rankId = this.tagList[this.tagsListNum].sourceid;
        this.title = this.tagList[this.tagsListNum].name;
        this.getRankList(1);
      },
      // immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getTagRank();
    this.getRankList(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.main_con {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  .con_l {
    min-height: 300px;
    min-width: 135px;
    max-width: 250px;
    background: #fff;
    margin-right: 1%;
    width: 18%;
    // box-shadow: 0 0 30px 0 rgb(65 67 70 / 10%);
    .con {
      overflow: hidden;
      margin: 5px;
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 10%);

      .tabs {
        margin: 20px 15% 14px;
        justify-content: space-between;
        font-size: 16px;
        font-weight: 300;
        padding-bottom: 13px;
        border-bottom: 1px solid #f2f2f2;
        span {
          position: relative;
          z-index: 2;
          height: 22px;
          line-height: 22px;
          cursor: pointer;
          &.active {
            position: relative;
            font-weight: 600;
            color: #000;
            &:after {
              position: absolute;
              content: " ";
              left: 0;
              bottom: 0;
              width: 100%;
              height: 6px;
              background: #31c27c;
              z-index: -1;
            }
            &:hover {
              color: #000;
            }
          }
          &:hover {
            color: #31c27c;
          }
        }
      }
      .tab_con {
        padding-bottom: 14px;
        li {
          height: 60px;
          padding: 0 15%;
          &.active {
            background: #f2f2f2;
          }
          .item_info {
            flex: 1;
          }
          .name {
            height: 20px;
            line-height: 20px;
            font-weight: 600;
            margin-bottom: 6px;
            cursor: pointer;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            padding-right: 5%;
            font-size: 15px;
          }
          .time {
            height: 20px;
            line-height: 20px;
            font-size: 13px;
            color: #999;
          }
        }
      }
    }
  }
  .con_r {
    position: relative;
    width: 76.14%;
    z-index: 10;
    margin-left: 10px;
    padding: 0;
    .title,
    .update_time {
      vertical-align: baseline;
    }
    .title {
      display: inline-block;
      margin: 20px 0 0;
      height: 30px;
      line-height: 30px;
      font-size: 28px;
      font-weight: 600;
    }
    .update_time {
      height: 20px;
      line-height: 20px;
      margin-left: 10px;
      color: #999;
    }
    .btns {
      margin: 20px 0 20px;
      .play {
        width: 130px;
        background: #31c27c;
        color: #fff;
        border: none;
        &:hover {
          color: #000;
        }
        &:active {
          box-shadow: 0 0 10px 0 rgb(65 67 70 / 30%) inset;
          border: none;
        }
      }
    }
    .list_out {
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
    }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>




